:root {
  --spacer-bg: #a8a88a;
  --accent-bg: #201060;
  --accent-text-bright: #ffffff;
  --accent-text: #d8d8d8;
  --accent-text-light: #b8b8b8;
  --averages-bg: #ffffff;
  --averages-color: #000000;
}

* {
  box-sizing: border-box;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Helvetica", sans-serif;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: none;
  white-space: nowrap;
}

.page {
  height: 100vh;
}

.spacer {
  height: 2px;
  background: #a8a88a;
  background: var(--spacer-bg);
}

.weightInputSection {
  height: 4rem;
}

.weightsSection {
  height: calc(45% - 4rem);
}

.averagesSection {
  height: 20%;
}

.graphSection {
  height: 35%;
}

